.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-list__link {
  display: block;
  padding-top: 8px;
  padding-bottom: 7px;

  font-family: "PT Sans Bold", "Arial", sans-serif;
  font-size: 18px;
  line-height: 40px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;

  border-bottom: 1px solid #678fa8;

  background-color: #81b3d2;

  &:hover {
    background-color: #669ec0;
  }

  &:active,
  &:focus {
    background-color: #5496bd;
    color: #94b9d2;
  }
 }

@media (min-width: @tablet-width) {
  .site-list {
    display: flex;
    justify-content: flex-start;
  }

  .site-list__item:nth-child(2) {
    margin-right: 154px;
  }

  .site-list__link {
    width: 154px;
    padding-top: 14px;
    padding-bottom: 15px;

    font-size: 14px;
    line-height: 26px;
    color: #000000;
    text-align: center;

    border: none;

    &:hover {
      color: #81b3d2;
    }

    &:active,
    &:focus {
      color: #cacaca;
    }
  }

  .site-list__link--active {
    color: #766357;

    &:hover {
      color: #604e43;
    }

    &:active,
    &:focus {
      color: #dbd8d5;
    }
  }
}

@media (min-width: @desktop-width) {
  .site-list__link {
    width: 240px;
  }

  .site-list__item:nth-child(2) {
    margin-right: 240px;
  }
}
