.advantages {
  text-align: center;
  text-transform: uppercase;
}
.advantages__wrapper {
  position: relative;
  padding: 29px 27px 27px 27px;
}
.photo-page .advantages__wrapper {
  padding: 28px 25px 29px 25px;
}
.form-page .advantages__wrapper {
  padding: 28px 20px 18px 20px;
}
.advantages__title {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 29px;
  font-family: "PT Sans Bold", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
}
.photo-page .advantages__title,
.form-page .advantages__title {
  margin-bottom: 28px;
  line-height: 24px;
}
.advantages__text {
  margin: 0;
  line-height: 26px;
}
.photo-page .advantages__text,
.form-page .advantages__text {
  line-height: 24px;
}
.advantages__triangle {
  position: absolute;
  left: 0;
  bottom: -15px;
}
@media (min-width: 768px) {
  .advantages__wrapper {
    width: 490px;
    margin: 0 auto;
    padding: 40px 0 49px 0;
    text-align: center;
  }
  .photo-page .advantages__wrapper {
    padding: 70px 0 67px 0;
  }
  .form-page .advantages__wrapper {
    padding: 67px 0 75px 0;
  }
  .advantages__title {
    margin-bottom: 30px;
    padding: 0 24px;
    font-size: 21px;
    line-height: 26px;
  }
  .photo-page .advantages__title,
  .form-page .advantages__title {
    margin-bottom: 30px;
    font-size: 30px;
    line-height: 26px;
  }
  .photo-page .advantages__text {
    width: 435px;
    text-align: center;
    margin-left: 27px;
  }
  .photo-page .advantages__text span {
    display: block;
  }
  .advantages__triangle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .photo-page .advantages__wrapper {
    padding-top: 105px;
  }
  .form-page .advantages__wrapper {
    padding-top: 105px;
    padding-bottom: 57px;
  }
}
.btn {
  display: block;
  padding: 16px 0;
  font-family: "PT Sans Bold", "Arial", sans-serif;
  font-size: 21px;
  line-height: 26px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
}
.btn:hover {
  background-color: #669ec0;
}
.btn:active,
.btn:focus {
  color: #94b9d2;
  background-color: #5496bd;
}
.card {
  margin: 0;
  padding: 0;
  list-style: none;
  border-bottom: 1px solid #e5e5e5;
}
.card:first-child {
  border-top: 1px solid #e5e5e5;
}
.card a {
  display: block;
}
.card__image {
  display: block;
  width: 280px;
  height: 280px;
}
.card__wrapper {
  padding-top: 16px;
}
.card__name {
  display: block;
  margin: 0;
  font-family: "PT Sans Bold", "Arial", sans-serif;
  text-transform: uppercase;
}
.card__like {
  margin-top: 5px;
}
.card__icon {
  fill: #b2b2b2;
}
.card__quantity {
  margin-left: 10px;
  font-family: "PT Sans Bold", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .card {
    border: none;
  }
  .card:first-child {
    border: none;
  }
  .card__image {
    width: 324px;
    height: 270px;
  }
  .card__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 15px 32px 15px 20px;
    background-color: #f2f2f2;
  }
  .card:first-child .card__wrapper {
    padding-right: 20px;
  }
  .card:nth-child(3) .card__wrapper {
    padding-right: 37px;
    background-color: #eaeaea;
  }
  .card__like {
    margin-top: 10px;
    margin-left: 5px;
  }
}
@media (min-width: 1200px) {
  .card__wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    box-sizing: border-box;
    color: #ffffff;
    background-color: #000000;
    opacity: 0.4;
  }
  .card:nth-child(3) .card__wrapper {
    background-color: #000000;
  }
  .card__image {
    width: 400px;
    height: 270px;
  }
}
.contacts__map {
