.control-panel__range {
  width: 240px;
  height: 3px;
  margin: 0 auto;
  background-color: #ffffff;
}
.control-panel__point {
  position: absolute;
  top: 19px;
  left: 58px;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background-color: #ffffff;
}
.control-panel__subtitles {
  order: 1;
  width: 70px;
  padding: 23px 0 23px 34px;
  box-sizing: border-box;
  background-color: #f2f2f2;
}
.control-panel__full-srceen {
  order: 1;
  width: 70px;
  padding: 23px 0 23px 27px;
  box-sizing: border-box;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .control-panel {
    bottom: 0;
    left: 60px;
    width: 648px;
    padding: 35px 0 35px 35px;
    box-sizing: border-box;
    background-color: #000000;
    opacity: 0.7;
  }
  .control-panel svg {
    fill: #ffffff;
  }
  .control-panel__replay {
    order: 0;
    width: 60px;
    padding: 0;
    background-color: inherit;
  }
  .control-panel__progress-bar {
    width: 385px;
    padding: 0;
  }
  .control-panel__range {
    width: 385px;
    margin-top: 10px;
  }
  .control-panel__point {
    top: 7px;
    left: 111px;
  }
  .control-panel__subtitles {
    width: 90px;
    background-color: inherit;
    padding: 0;
    padding-left: 45px;
    box-sizing: border-box;
  }
  .control-panel__full-srceen {
    width: 70px;
    background-color: inherit;
    padding: 0;
    padding-left: 15px;
    box-sizing: border-box;
  }
}
@media (min-width: 1200px) {
  .control-panel {
    width: 800px;
    padding: 35px 0 40px 35px;
  }
  .control-panel__progress-bar {
    width: 540px;
    padding: 0;
  }
  .control-panel__range {
    width: 540px;
    margin-top: 10px;
  }
