.page-header {
  position: relative;
  display: flex;
  flex-direction: column;

  background-color: #81b3d2;
}

.page-header__logo {
  display: block;
  width: 100%;
  height: 55px;
  margin: 0 auto;

  background-color: #ffffff;
}

.page-header__logo-image {
  display: block;
  margin: 0 auto;
  width: 102px;
  height: 82px;
}

@media (min-width: @tablet-width) {
  .page-header {
    display: block;
    background-color: #ffffff;
  }

  .page-header__logo {
    position: absolute;
    top: 0;
    left: 40%;

    width: 154px;
  }
}

@media (min-width: @desktop-width) {
  .page-header__logo {
    width: 190px;

    top: 0;
    left: 42%;
  }

  .page-header__logo-image {
    width: 190px;
    height: 100px;
  }
}
